  "use client"

  import { Card } from "@/components/ui/card"
  import { Button } from "@/components/ui/button"
  import { Trophy, Download, PartyPopper } from "lucide-react"
  import type { Winner } from "@/components/random-picker"
  import * as XLSX from "xlsx"
  interface WinnerDisplayProps {
    winner: Winner
  }
  export function WinnerDisplay({ winner }: WinnerDisplayProps) {
    const handleDownloadResults = (format: "txt" | "excel") => {
      const dateStr = winner.timestamp.toLocaleDateString()
      const timeStr = winner.timestamp.toLocaleTimeString()
      const fullDateTime = winner.timestamp.toLocaleString()

      if (format === "txt") {
        const result = `ðŸŽ‰ WINNER ANNOUNCEMENT ðŸŽ‰
        
  Winner Name: ${winner.winnerName}
  Prize: ${winner.prizeName}
  Date: ${dateStr}
  Time: ${timeStr}
  Full Timestamp: ${fullDateTime}

  Congratulations to the winner!`

        const blob = new Blob([result], { type: "text/plain" })
        const url = URL.createObjectURL(blob)
        const a = document.createElement("a")
        a.href = url
        a.download = `winner-${winner.prizeName.replace(/\s+/g, "-")}-${winner.timestamp.toISOString().split("T")[0]}.txt`
        document.body.appendChild(a)
        a.click()
        document.body.removeChild(a)
        URL.revokeObjectURL(url)
      } else if (format === "excel") {
        const data = [
          ["Random Picker - Winner Results"],
          [],
          ["Winner Name", winner.winnerName],
          ["Prize", winner.prizeName],
          ["Date", dateStr],
          ["Time", timeStr],
          ["Full Timestamp", fullDateTime],
        ]

        const ws = XLSX.utils.aoa_to_sheet(data)
        ws["!cols"] = [{ width: 20 }, { width: 30 }]

        const wb = XLSX.utils.book_new()
        XLSX.utils.book_append_sheet(wb, ws, "Winner")

        XLSX.writeFile(
          wb,
          `winner-${winner.prizeName.replace(/\s+/g, "-")}-${winner.timestamp.toISOString().split("T")[0]}.xlsx`,
        )
      }
    }

    return (
      <Card className="p-6 bg-gradient-to-br from-primary/10 via-secondary/10 to-accent/10 border-2 border-primary/20 animate-in fade-in zoom-in duration-500">
        <div className="text-center space-y-6">
          <div className="flex justify-center gap-4">
            <PartyPopper className="h-12 w-12 text-secondary animate-bounce" />
            <Trophy className="h-16 w-16 text-primary" />
            <PartyPopper className="h-12 w-12 text-accent animate-bounce" />
          </div>

          <div>
            <h2 className="text-3xl font-bold mb-2 text-balance">{"ðŸŽ‰ Congratulations! ðŸŽ‰"}</h2>
            <p className="text-muted-foreground">{"We have a winner!"}</p>
          </div>

          <div className="bg-card p-6 rounded-lg border-2 border-primary/30 space-y-2">
            <p className="text-xl font-bold text-primary">{winner.winnerName}</p>
            <p className="text-lg">{"won"}</p>
            <p className="text-2xl font-bold text-secondary">{winner.prizeName}</p>
            <p className="text-sm text-muted-foreground mt-4">
              {winner.timestamp.toLocaleDateString()} at {winner.timestamp.toLocaleTimeString()}
            </p>
          </div>

          <div className="space-y-2">
            <Button onClick={() => handleDownloadResults("txt")} variant="outline" className="w-full bg-transparent">
              <Download className="h-4 w-4 mr-2" />
              {"Download Results (TXT)"}
            </Button>
            <Button onClick={() => handleDownloadResults("excel")} variant="outline" className="w-full bg-transparent">
              <Download className="h-4 w-4 mr-2" />
              {"Download Results (Excel)"}
            </Button>
          </div>
        </div>
      </Card>
    )
  }
